.clients {
  padding: 100px 0px;
  overflow: hidden;
}

.clients__controls {
  display: flex;
  align-items: center;
}

.clients__controls-wrap {
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.clients__control {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  width: 35px;
  height: 20px;
  transition: transform 0.3s ease-out 0s;
  flex-shrink: 0;
  background-image: url(../img/slider-arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.clients__control:hover {
  transform: scale(1.1);
}

.clients__control_next {
  transform: rotate(180deg);
}

.clients__control_next:hover {
  transform: rotate(180deg) scale(1.1);
}

.clients__pagination {
  margin: 0px 15px;
  font-size: 25px;
  color: #C1C1C1;
}

.clients__pagination .swiper-pagination-current {
  color: #000000;
  font-size: 36px;
}

.clients__pagination .swiper-pagination-total {
  font-size: 25px;
  color: #C1C1C1;
}

.clients__swiper {
  margin-top: 60px;
}

.clients__swiper .swiper-wrapper {
  align-items: baseline;
}
