.catalog {
  padding: 90px 0px 100px;
  overflow: hidden;
}

.catalog__deco {
  position: absolute;
  right: 0;
  top: 0;
}

.catalog__list {
  margin: 80px 0px 200px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(390px, 1fr));
  column-gap: 40px;
  row-gap: 55px;
}

.catalog__item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.catalog__icon + .catalog__name {
  margin-left: 50px;
}

.catalog__icon {
  width: 100px;
  height: auto;
  object-fit: contain;
}

.catalog__name {
  font-size: 26px;
  line-height: 38px;
}

.catalog__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
