.swiper-cover {
  z-index: 3;
  position: absolute;
  width: 100%;
  height: 560px;
  left: 0;
  pointer-events: none;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.8) 2%, rgba(65, 53, 240, 0) 20%, rgba(65, 53, 240, 0) 80%, rgba(255, 255, 255, 0.8) 97%);
}

.swiper-cover__deco {
  position: absolute;
  top: 0;
}

.swiper-cover_reverse .swiper-cover__deco {
  right: 0;
}

.swiper-cover_reverse {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.9) 6%, rgba(65, 53, 240, 0) 20%, rgba(65, 53, 240, 0) 80%, rgba(255, 255, 255, 0.9) 94%);
}
