@import "./varaibles";

.clients {
    padding: 100px 0px;
    overflow: hidden;
    &__controls {
        display: flex;
        align-items: center;
        &-wrap {
            margin-top: 50px;
            display: flex;
            align-items: center;
            justify-content: flex-end; } }
    &__control {
        background-color: transparent;
        border: none;
        cursor: pointer;
        outline: none;
        width: 35px;
        height: 20px;
        @include transition(transform);
        flex-shrink: 0;
        background: {
            image: url(../img/slider-arrow.svg);
            repeat: no-repeat;
            position: center;
            size: contain; }
        &:hover {
            transform: scale(1.1); }
        &_prev {}
        &_next {
            transform: rotate(180deg);
            &:hover {
                transform: rotate(180deg) scale(1.1); } } }
    &__pagination {
        margin: 0px 15px;
        font-size: 25px;
        color: #C1C1C1;
        .swiper-pagination-current {
            color: $text-color;
            font-size: 36px; }
        .swiper-pagination-total {
            font-size: 25px;
            color: #C1C1C1; } }
    &__swiper {
        margin-top: 60px;
        .swiper-wrapper {
            align-items: baseline; } } }
